import { Skeleton } from "@/components/ui/skeleton";

const MapViewSkeleton = () => {
  return (
    <div className="relative flex-1 min-h-[300px] sm:min-h-[400px] lg:mx-4 lg:mb-4 lg:rounded-lg overflow-hidden lg:shadow-lg bg-muted">
      {/* Map background skeleton */}
      <Skeleton className="absolute inset-0 w-full h-full rounded-none" />
      
      {/* Fake map markers */}
      <div className="absolute inset-0 flex items-center justify-center">
        <div className="relative w-full h-full">
          {/* Center marker */}
          <div className="absolute top-1/2 left-1/2 -translate-x-1/2 -translate-y-1/2">
            <Skeleton className="h-8 w-8 rounded-full" />
          </div>
          
          {/* Scattered markers */}
          <div className="absolute top-1/4 left-1/3">
            <Skeleton className="h-6 w-6 rounded-full" />
          </div>
          <div className="absolute top-1/3 right-1/4">
            <Skeleton className="h-6 w-6 rounded-full" />
          </div>
          <div className="absolute bottom-1/3 left-1/4">
            <Skeleton className="h-6 w-6 rounded-full" />
          </div>
          <div className="absolute bottom-1/4 right-1/3">
            <Skeleton className="h-6 w-6 rounded-full" />
          </div>
        </div>
      </div>
      
      {/* Bottom left logo placeholder */}
      <div className="absolute bottom-4 left-4 z-10">
        <Skeleton className="h-10 w-36 rounded-full" />
      </div>
      
      {/* Map controls placeholder (top right) */}
      <div className="absolute top-4 right-4 z-10 flex flex-col gap-2">
        <Skeleton className="h-8 w-8 rounded-md" />
        <Skeleton className="h-8 w-8 rounded-md" />
      </div>
    </div>
  );
};

export default MapViewSkeleton;