import { useState, useEffect, useRef, useCallback } from "react";
import { useNavigate } from "react-router-dom";
import { Card, CardContent, CardFooter, CardHeader } from "@/components/ui/card";
import { Button } from "@/components/ui/button";
import { Input } from "@/components/ui/input";
import { Badge } from "@/components/ui/badge";
import { Popover, PopoverContent, PopoverTrigger } from "@/components/ui/popover";
import { Star, MapPin, Heart, Phone, Clock, Loader2, Map, List, Navigation, MoreHorizontal, Filter, X } from "lucide-react";
import { Link } from "react-router-dom";
import { useRestaurants, useFavorites, useToggleFavorite, useRestaurantTypes } from "@/hooks/useRestaurants";
import { useAuth } from "@/contexts/AuthContext";
import { useTranslation } from "react-i18next";
import { calculateDistance, getUserLocation, openNavigation } from "@/lib/geolocation";
import NavigationConfirmDialog from "@/components/NavigationConfirmDialog";
import RestaurantMap from "@/components/RestaurantMap";
import { useToast } from "@/hooks/use-toast";
import { useLocalizedContent } from "@/hooks/useLocalizedContent";
import { formatOpeningHours } from "@/lib/timeUtils";
import { useActivities } from "@/hooks/useActivities";
import { supabase } from "@/integrations/supabase/client";
import { useActivityRestaurantCounts } from "@/hooks/useActivityRestaurantCounts";
import { useInfiniteScroll } from "@/hooks/useInfiniteScroll";
import logoIcon from "@/assets/xiaoxiong-logo-clean.png";
import LocationPermissionDialog from "@/components/LocationPermissionDialog";
import { RestaurantListSkeleton, MapViewSkeleton } from "@/components/skeletons";
import { PullToRefresh } from "@/components/PullToRefresh";
import { useQueryClient } from "@tanstack/react-query";

const RestaurantTags = ({
  restaurant,
  language
}: {
  restaurant: any;
  language: string;
}) => {
  const [visibleCount, setVisibleCount] = useState(999);
  const containerRef = useRef<HTMLDivElement>(null);
  const typeTags = restaurant.type.map((type: string, idx: number) => ({
    key: `type-${idx}`,
    label: type,
    variant: "secondary" as const,
    color: undefined
  }));
  const allTags = typeTags;
  useEffect(() => {
    const checkOverflow = () => {
      if (!containerRef.current) return;
      const container = containerRef.current;
      const badges = Array.from(container.querySelectorAll('.tag-badge')) as HTMLElement[];
      if (badges.length === 0) {
        setTimeout(checkOverflow, 50);
        return;
      }
      
      const firstBadgeTop = badges[0]?.offsetTop;
      let lastVisibleIndex = badges.length;

      // Find where tags overflow to second line
      for (let i = 0; i < badges.length; i++) {
        if (badges[i].offsetTop > firstBadgeTop) {
          lastVisibleIndex = i;
          break;
        }
      }

      // If all tags fit, no need for "..." button
      if (lastVisibleIndex === badges.length) {
        setVisibleCount(badges.length);
        return;
      }

      // Calculate available width and check if we need to reduce for "..." button
      const containerWidth = container.offsetWidth;
      const moreButtonWidth = 40; // Approximate width for "..." button
      
      // Calculate total width of visible tags
      let totalWidth = 0;
      let adjustedIndex = lastVisibleIndex;
      
      for (let i = 0; i < lastVisibleIndex; i++) {
        totalWidth += badges[i].offsetWidth + 8; // 8px gap
      }
      
      // If visible tags + button would overflow, reduce by 1
      if (totalWidth + moreButtonWidth > containerWidth) {
        adjustedIndex = Math.max(1, lastVisibleIndex - 1);
      }
      
      setVisibleCount(adjustedIndex);
    };
    setVisibleCount(999);
    const timer = setTimeout(checkOverflow, 100);
    window.addEventListener('resize', checkOverflow);
    return () => {
      clearTimeout(timer);
      window.removeEventListener('resize', checkOverflow);
    };
  }, [allTags.length, restaurant.id]);
  const visibleTags = allTags.slice(0, visibleCount);
  const hiddenTags = allTags.slice(visibleCount);
  const hasMore = hiddenTags.length > 0;
  return <div className="flex items-center gap-2 -mt-1">
      <div ref={containerRef} className="flex gap-2 flex-wrap flex-1">
        {visibleTags.map(tag => <Badge key={tag.key} variant={tag.variant} className="text-xs whitespace-nowrap tag-badge" style={tag.color ? {
        backgroundColor: tag.color,
        color: 'white'
      } : undefined}>
            {tag.label}
          </Badge>)}
      </div>
      {hasMore && <Popover>
          <PopoverTrigger asChild>
            <Button variant="ghost" size="sm" className="h-6 px-2 text-muted-foreground hover:text-foreground flex-shrink-0">
              <MoreHorizontal className="h-4 w-4" />
            </Button>
          </PopoverTrigger>
          <PopoverContent className="w-auto max-w-80">
            <div className="flex flex-wrap gap-2">
              {hiddenTags.map(tag => <Badge key={tag.key} variant={tag.variant} className="text-xs" style={tag.color ? {
            backgroundColor: tag.color,
            color: 'white'
          } : undefined}>
                  {tag.label}
                </Badge>)}
            </div>
          </PopoverContent>
        </Popover>}
    </div>;
};
const Restaurants = () => {
  const navigate = useNavigate();
  const queryClient = useQueryClient();
  const {
    t,
    i18n
  } = useTranslation();
  const {
    toast
  } = useToast();
  const {
    localizeRestaurant
  } = useLocalizedContent();
  const [searchQuery, setSearchQuery] = useState("");
  const [selectedTypes, setSelectedTypes] = useState<string[]>([]);
  const [selectedActivity, setSelectedActivity] = useState<string | null>(null);
  const [activityRestaurantIds, setActivityRestaurantIds] = useState<string[]>([]);
  const [viewMode, setViewMode] = useState<"list" | "map">("list");
  const [radiusFilter, setRadiusFilter] = useState<number | null>(null);
  const [userLocation, setUserLocation] = useState<{
    lat: number;
    lng: number;
  } | null>(null);
  const [sortByDistance, setSortByDistance] = useState(false);
  const [showAllTypes, setShowAllTypes] = useState(false);
  const [showAllActivities, setShowAllActivities] = useState(false);
  const [showNavigationDialog, setShowNavigationDialog] = useState(false);
  const [showLocationDialog, setShowLocationDialog] = useState(false);
  const [navigationTarget, setNavigationTarget] = useState<{ lat: number; lng: number; name: string } | null>(null);
  const {
    user
  } = useAuth();
  const {
    data: restaurants,
    isLoading
  } = useRestaurants(searchQuery, selectedTypes.length > 0 ? selectedTypes.join(',') : undefined);
  const {
    data: favoriteIds = []
  } = useFavorites();
  const {
    data: restaurantTypes = []
  } = useRestaurantTypes();
  const {
    data: activities = []
  } = useActivities();
  const { data: activityRestaurantCounts = {} } = useActivityRestaurantCounts();

  const handleRefresh = useCallback(async () => {
    await Promise.all([
      queryClient.invalidateQueries({ queryKey: ['restaurants'] }),
      queryClient.invalidateQueries({ queryKey: ['activities'] }),
      queryClient.invalidateQueries({ queryKey: ['restaurantTypes'] }),
    ]);
  }, [queryClient]);

  // Filter activities to only show those with at least 1 restaurant
  const activitiesWithRestaurants = activities.filter(activity => (activityRestaurantCounts[activity.id] || 0) > 0);
  const toggleFavorite = useToggleFavorite();
  const handleToggleFavorite = (restaurantId: string, e: React.MouseEvent) => {
    e.preventDefault();
    e.stopPropagation();
    if (!user) {
      return;
    }
    toggleFavorite.mutate(restaurantId);
  };
  const handleTypeFilter = (type: string) => {
    setSelectedTypes(prev => {
      if (prev.includes(type)) {
        // Remove type if already selected
        return prev.filter(t => t !== type);
      } else {
        // Add type to selection
        return [...prev, type];
      }
    });
  };
  const handleRequestLocation = () => {
    setShowLocationDialog(true);
  };
  
  const requestLocation = async () => {
    setShowLocationDialog(false);
    try {
      const position = await getUserLocation();
      setUserLocation({
        lat: position.coords.latitude,
        lng: position.coords.longitude
      });
      setSortByDistance(true);
      toast({
        title: t("restaurants.locationEnabled"),
        description: t("restaurants.sortByDistance")
      });
    } catch (error: any) {
      console.error("Location error:", error);
      // Silently handle location errors - no toast notification
    }
  };
  const handleRadiusChange = (radius: number | null) => {
    setRadiusFilter(radius);
    if (radius && !userLocation) {
      requestLocation();
    }
  };
  const handleActivityFilter = async (activityId: string) => {
    if (selectedActivity === activityId) {
      setSelectedActivity(null);
      setActivityRestaurantIds([]);
    } else {
      setSelectedActivity(activityId);
      // Fetch restaurants for this activity
      const {
        data,
        error
      } = await supabase.from("activity_restaurants").select("restaurant_id").eq("activity_id", activityId);
      if (error) {
        console.error("Error fetching activity restaurants:", error);
        setActivityRestaurantIds([]);
      } else {
        setActivityRestaurantIds(data.map(ar => ar.restaurant_id));
      }
    }
  };

  // Removed automatic location request - now manual only

  // Localize and process restaurants
  const processedRestaurants = restaurants?.map(restaurant => {
    const localized = localizeRestaurant(restaurant);
    if (userLocation) {
      const distance = calculateDistance(userLocation.lat, userLocation.lng, restaurant.lat, restaurant.lng);
      return {
        ...localized,
        distance
      };
    }
    return localized;
  }) || [];

  // Filter by activity - when activity is selected but has no restaurants, show empty list
  const activityFilteredRestaurants = selectedActivity 
    ? processedRestaurants.filter(r => activityRestaurantIds.includes(r.id)) 
    : processedRestaurants;

  // Filter by radius
  const filteredRestaurants = radiusFilter && userLocation ? activityFilteredRestaurants.filter(r => r.distance && r.distance <= radiusFilter) : activityFilteredRestaurants;
  // Sort: by distance if location available, otherwise by rating
  const sortedRestaurants = userLocation 
    ? [...filteredRestaurants].sort((a, b) => (a.distance || 0) - (b.distance || 0))
    : [...filteredRestaurants].sort((a, b) => (b.rating || 0) - (a.rating || 0));

  // Infinite scroll for list view
  const { displayedData: displayedRestaurants, hasMore, loadMore, reset } = useInfiniteScroll({
    data: sortedRestaurants,
    initialItemsPerPage: 12,
    incrementPerPage: 12,
  });

  // Reset infinite scroll when filters change
  useEffect(() => {
    reset();
  }, [searchQuery, selectedTypes, selectedActivity, radiusFilter, sortByDistance, reset]);

  const handleRestaurantClick = (restaurant: any) => {
    navigate(`/restaurant/${restaurant.id}`);
  };
  const pageContent = (
    <div className={`bg-secondary/20 animate-fade-in ${viewMode === "map" ? "h-screen flex flex-col overflow-hidden" : "min-h-screen py-8 px-4"}`}>
      <div className={`${viewMode === "map" ? "flex flex-col h-full" : "container mx-auto max-w-7xl"}`}>
        {/* Header */}
        <div className={`${viewMode === "map" ? "px-4 pt-4 pb-2 flex-shrink-0" : "mb-8"}`}>
          <h1 className={`text-4xl font-bold text-primary ${viewMode === "map" ? "hidden md:block md:mb-4" : "mb-4"}`}>{t("restaurants.title")}</h1>
          
          {/* Search and Filters */}
          <div className="flex flex-col gap-4">
            {/* Desktop/Tablet: Search + Buttons in one row */}
            <div className="flex flex-col md:flex-row gap-4 md:items-center">
              <Input 
                placeholder={t("restaurants.search")} 
                value={searchQuery} 
                onChange={e => setSearchQuery(e.target.value)} 
                className="w-full md:max-w-md" 
              />
              
              {/* View Mode Toggle, Filter and Location Button */}
              <div className="flex gap-2 items-center">
                <Button variant={viewMode === "list" ? "default" : "outline"} size="icon" onClick={() => setViewMode("list")} title={t("restaurants.listView")} className="flex-shrink-0">
                  <List className="w-4 h-4" />
                </Button>
                <Button variant={viewMode === "map" ? "default" : "outline"} size="icon" onClick={() => setViewMode("map")} title={t("restaurants.mapView")} className="flex-shrink-0">
                  <Map className="w-4 h-4" />
                </Button>
                
                {/* Filter Button with Popover */}
                <Popover>
                  <PopoverTrigger asChild>
                    <Button 
                      variant={(selectedTypes.length > 0 || selectedActivity || radiusFilter) ? "default" : "outline"} 
                      size="icon"
                      className="flex-shrink-0 relative"
                      title={t("restaurants.filter")}
                    >
                      <Filter className="w-4 h-4" />
                      {(selectedTypes.length > 0 || selectedActivity || radiusFilter) && (
                        <Badge variant="secondary" className="absolute -top-1 -right-1 h-5 w-5 p-0 flex items-center justify-center text-xs">
                          {(selectedTypes.length > 0 ? 1 : 0) + (selectedActivity ? 1 : 0) + (radiusFilter ? 1 : 0)}
                        </Badge>
                      )}
                    </Button>
                  </PopoverTrigger>
                  <PopoverContent className="w-[90vw] max-w-sm p-0" align="start">
                    <div className="flex flex-col">
                      {/* Filter Header */}
                      <div className="flex items-center justify-between px-4 py-3 border-b border-border/50 bg-muted/30">
                        <span className="font-semibold text-foreground text-base">{t("restaurants.filter")}</span>
                        {(selectedTypes.length > 0 || selectedActivity || radiusFilter) && (
                          <Button
                            variant="ghost"
                            size="sm"
                            className="h-7 px-2 text-xs text-destructive hover:text-destructive hover:bg-destructive/10"
                            onClick={() => {
                              setSelectedTypes([]);
                              setSelectedActivity(null);
                              setActivityRestaurantIds([]);
                              setRadiusFilter(null);
                            }}
                          >
                            <X className="w-3 h-3 mr-1" />
                            {t("common.actions.clearFilter")}
                          </Button>
                        )}
                      </div>

                      <div className="p-4 space-y-5">
                        {/* Type Filters Section */}
                        <div className="space-y-3">
                          <div className="flex items-center justify-between">
                            <span className="text-sm font-medium text-muted-foreground">{t("restaurants.restaurantTypes")}</span>
                            <div className="flex items-center gap-2">
                              {selectedTypes.length > 0 && (
                                <Button
                                  variant="ghost"
                                  size="sm"
                                  className="h-6 px-2 text-xs text-destructive hover:text-destructive hover:bg-destructive/10"
                                  onClick={() => setSelectedTypes([])}
                                >
                                  <X className="w-3 h-3 mr-1" />
                                  {t("common.actions.clear")}
                                </Button>
                              )}
                              {restaurantTypes.length > 8 && (
                                <Button
                                  variant="link"
                                  size="sm"
                                  className="h-auto p-0 text-xs text-primary hover:text-primary/80"
                                  onClick={() => setShowAllTypes(!showAllTypes)}
                                >
                                  {showAllTypes ? t("common.showLess") : t("common.showMore")}
                                </Button>
                              )}
                            </div>
                          </div>
                          <div className="flex gap-2 flex-wrap max-h-36 overflow-y-auto pr-1">
                            {(showAllTypes ? restaurantTypes : restaurantTypes.slice(0, 8)).map((type) => (
                              <Badge
                                key={type}
                                variant={selectedTypes.includes(type) ? "default" : "outline"}
                                className={`cursor-pointer transition-all duration-200 px-3 py-1.5 text-xs font-normal whitespace-nowrap hover:bg-primary/10 ${
                                  selectedTypes.includes(type) 
                                    ? 'bg-primary text-primary-foreground shadow-sm' 
                                    : 'bg-background hover:border-primary/50'
                                }`}
                                onClick={() => handleTypeFilter(type)}
                              >
                                {type}
                              </Badge>
                            ))}
                          </div>
                        </div>

                        {/* Activity Filters Section */}
                        {activitiesWithRestaurants.length > 0 && (
                          <div className="space-y-3">
                            <div className="flex items-center justify-between">
                              <span className="text-sm font-medium text-muted-foreground">{t("nav.activities")}</span>
                              <div className="flex items-center gap-2">
                                {selectedActivity && (
                                  <Button
                                    variant="ghost"
                                    size="sm"
                                    className="h-6 px-2 text-xs text-destructive hover:text-destructive hover:bg-destructive/10"
                                    onClick={() => {
                                      setSelectedActivity(null);
                                      setActivityRestaurantIds([]);
                                    }}
                                  >
                                    <X className="w-3 h-3 mr-1" />
                                    {t("common.actions.clear")}
                                  </Button>
                                )}
                                {activitiesWithRestaurants.length > 8 && (
                                  <Button
                                    variant="link"
                                    size="sm"
                                    className="h-auto p-0 text-xs text-primary hover:text-primary/80"
                                    onClick={() => setShowAllActivities(!showAllActivities)}
                                  >
                                    {showAllActivities ? t("common.showLess") : t("common.showMore")}
                                  </Button>
                                )}
                              </div>
                            </div>
                            <div className="flex gap-2 flex-wrap max-h-36 overflow-y-auto pr-1">
                              {(showAllActivities ? activitiesWithRestaurants : activitiesWithRestaurants.slice(0, 8)).map((activity) => {
                                const badgeText = i18n.language === 'zh' 
                                  ? (activity.badge_text_zh || activity.title_zh)
                                  : i18n.language === 'pt' 
                                  ? (activity.badge_text_pt || activity.title_pt)
                                  : (activity.badge_text_en || activity.title_en);
                                return (
                                  <Badge
                                    key={activity.id}
                                    variant={selectedActivity === activity.id ? "default" : "outline"}
                                    className={`cursor-pointer transition-all duration-200 px-3 py-1.5 text-xs font-normal whitespace-nowrap hover:bg-primary/10 ${
                                      selectedActivity === activity.id 
                                        ? 'bg-primary text-primary-foreground shadow-sm' 
                                        : 'bg-background hover:border-primary/50'
                                    }`}
                                    onClick={() => handleActivityFilter(activity.id)}
                                  >
                                    {badgeText}
                                  </Badge>
                                );
                              })}
                            </div>
                          </div>
                        )}

                        {/* Radius Filter Section */}
                        <div className="space-y-3">
                          <div className="flex items-center justify-between">
                            <span className="text-sm font-medium text-muted-foreground">{t("restaurants.radius")}</span>
                            {radiusFilter && (
                              <Button
                                variant="ghost"
                                size="sm"
                                className="h-6 px-2 text-xs text-destructive hover:text-destructive hover:bg-destructive/10"
                                onClick={() => setRadiusFilter(null)}
                              >
                                <X className="w-3 h-3 mr-1" />
                                {t("common.actions.clear")}
                              </Button>
                            )}
                          </div>
                          <div className="flex gap-2 flex-wrap">
                            {[
                              { value: null, label: t("restaurants.allRestaurants") },
                              { value: 5, label: t("restaurants.radius5km") },
                              { value: 10, label: t("restaurants.radius10km") },
                              { value: 20, label: t("restaurants.radius20km") },
                            ].map((option) => (
                              <Badge
                                key={option.value ?? 'all'}
                                variant={radiusFilter === option.value ? "default" : "outline"}
                                className={`cursor-pointer transition-all duration-200 px-3 py-1.5 text-xs font-normal whitespace-nowrap hover:bg-primary/10 ${
                                  radiusFilter === option.value 
                                    ? 'bg-primary text-primary-foreground shadow-sm' 
                                    : 'bg-background hover:border-primary/50'
                                }`}
                                onClick={() => handleRadiusChange(option.value)}
                              >
                                {option.label}
                              </Badge>
                            ))}
                          </div>
                        </div>
                      </div>
                    </div>
                  </PopoverContent>
                </Popover>

                {!userLocation ? (
                  <Button variant="outline" onClick={handleRequestLocation} className="flex-1 md:flex-initial">
                    <Navigation className="w-4 h-4 mr-2 flex-shrink-0" />
                    <span className="md:hidden whitespace-nowrap text-sm">{t("restaurants.getLocationMobile")}</span>
                    <span className="hidden md:inline whitespace-nowrap">{t("restaurants.getLocation")}</span>
                  </Button>
                ) : (
                  <Button 
                    variant={sortByDistance ? "default" : "outline"} 
                    onClick={() => setSortByDistance(!sortByDistance)} 
                    className="flex-1 md:flex-initial whitespace-nowrap"
                  >
                    {t("restaurants.sortByDistance")}
                  </Button>
                )}
              </div>
            </div>
          </div>
        </div>

        {/* Content Area */}
        {/* Loading State - Show skeleton immediately, don't wait for undefined */}
        {(isLoading || !restaurants) ? (
          viewMode === "map" ? (
            <MapViewSkeleton />
          ) : (
            <RestaurantListSkeleton count={6} />
          )
        ) : sortedRestaurants.length === 0 ? (
          /* Empty State */
          <div className={`text-center ${viewMode === "map" ? "flex-1 flex flex-col justify-center px-4" : "py-20"}`}>
            <p className="text-xl text-muted-foreground">{t("restaurants.noResults")}</p>
            <p className="text-muted-foreground mt-2">{t("restaurants.noResultsDesc")}</p>
          </div>
        ) : (
          /* Content */
          <>
            {/* Map View */}
            {viewMode === "map" && sortedRestaurants.length > 0 && (
              <div className="relative flex-1 min-h-[300px] sm:min-h-[400px] lg:mx-4 lg:mb-4 lg:rounded-lg overflow-hidden lg:shadow-lg">
                <RestaurantMap restaurants={sortedRestaurants} onRestaurantClick={handleRestaurantClick} userLocation={userLocation} radiusFilter={radiusFilter} showViewDetailsButton={true} />
                {/* Logo and text in bottom left corner of map */}
                <div className="absolute bottom-4 left-4 z-10 pointer-events-none flex items-center gap-2 bg-background/80 backdrop-blur-sm px-3 py-2 rounded-full shadow-lg">
                  <img 
                    src={logoIcon} 
                    alt="Xiaoxiong Market Logo" 
                    className="h-6 w-6 object-contain"
                  />
                  <span className="text-sm font-semibold text-foreground whitespace-nowrap">
                    {i18n.language === 'zh' ? '小熊市场' : 'Xiaoxiong Market'}
                  </span>
                </div>
              </div>
            )}

            {/* List View */}
            {viewMode === "list" && sortedRestaurants.length > 0 && (
              <>
                <div className="grid md:grid-cols-2 lg:grid-cols-3 gap-6">
                  {displayedRestaurants.map(restaurant => (
                    <Card key={restaurant.id} className="overflow-hidden hover:shadow-large transition-shadow flex flex-col h-full">
                      <div className="relative h-48 overflow-hidden">
                        <img src={restaurant.image_url || "https://images.unsplash.com/photo-1517248135467-4c7edcad34c4?w=600&q=80"} alt={restaurant.name} className="w-full h-full object-cover transition-transform hover:scale-105" />
                        {user && (
                          <Button size="icon" variant="secondary" className="absolute top-3 right-3" onClick={e => handleToggleFavorite(restaurant.id, e)}>
                            <Heart className={`w-5 h-5 ${favoriteIds.includes(restaurant.id) ? "fill-destructive text-destructive" : ""}`} />
                          </Button>
                        )}
                      </div>
                      
                      <CardHeader className="mb-px pb-[15px]">
                        <div className="flex items-start justify-between">
                          <div className="flex flex-col gap-2">
                            <h3 className="text-xl font-semibold text-card-foreground">
                              {restaurant.name}
                            </h3>
                            {restaurant.distance !== undefined && (
                              <div className="flex items-center gap-1">
                                <Navigation className="w-4 h-4 text-primary" />
                                <span className="font-semibold text-sm text-foreground">
                                  {restaurant.distance.toFixed(1)} km
                                </span>
                              </div>
                            )}
                          </div>
                          <div className="flex items-center gap-1">
                            <Star className={`w-5 h-5 ${restaurant.rating ? "fill-accent text-accent" : "text-muted-foreground"}`} />
                            {restaurant.rating ? (
                              <span className="font-semibold text-card-foreground">
                                {restaurant.rating.toFixed(1)}
                              </span>
                            ) : (
                              <span className="text-xs text-muted-foreground text-center whitespace-nowrap">
                                {t("restaurant.noRatingLine1")}<br />{t("restaurant.noRatingLine2")}
                              </span>
                            )}
                          </div>
                        </div>
                      </CardHeader>

                      <CardContent className="space-y-2 flex-grow">
                        {/* Type Tags */}
                        <RestaurantTags restaurant={restaurant} language={i18n.language} />
                        
                        <div className="flex items-center gap-2 text-muted-foreground text-sm">
                          <MapPin className="w-4 h-4 flex-shrink-0" />
                          <span className="line-clamp-1">{restaurant.address}</span>
                        </div>
                        {restaurant.phone && (
                          <div className="flex items-center gap-2 text-muted-foreground text-sm">
                            <Phone className="w-4 h-4 flex-shrink-0" />
                            <span>{restaurant.phone}</span>
                          </div>
                        )}
                        {restaurant.opening_hours && (
                          <div className="flex items-center gap-2 text-muted-foreground text-sm">
                            <Clock className="w-4 h-4 flex-shrink-0" />
                            <span>{formatOpeningHours(restaurant.opening_hours)}</span>
                          </div>
                        )}
                      </CardContent>

                      <CardFooter className="gap-2 mt-auto">
                        <Button asChild className="flex-1">
                          <Link to={`/restaurant/${restaurant.id}`}>{t("restaurants.viewDetails")}</Link>
                        </Button>
                        {userLocation && (
                          <Button variant="outline" size="icon" onClick={() => {
                            setNavigationTarget({ lat: restaurant.lat, lng: restaurant.lng, name: restaurant.name });
                            setShowNavigationDialog(true);
                          }} title={t("restaurants.directions")}>
                            <Navigation className="w-4 h-4" />
                          </Button>
                        )}
                      </CardFooter>
                    </Card>
                  ))}
                </div>
                
                {/* Load More Button */}
                {hasMore && (
                  <div className="flex justify-center mt-8">
                    <Button onClick={loadMore} size="lg" variant="outline">
                      {t("common.loadMore")} ({displayedRestaurants.length} / {sortedRestaurants.length})
                    </Button>
                  </div>
                )}
              </>
            )}
          </>
        )}

        <NavigationConfirmDialog
          open={showNavigationDialog}
          onOpenChange={setShowNavigationDialog}
          onConfirm={() => {
            if (navigationTarget) {
              openNavigation(navigationTarget.lat, navigationTarget.lng, navigationTarget.name);
            }
            setShowNavigationDialog(false);
          }}
          destinationName={navigationTarget?.name || ""}
        />

        <LocationPermissionDialog
          open={showLocationDialog}
          onOpenChange={setShowLocationDialog}
          onConfirm={requestLocation}
        />
      </div>
    </div>
  );

  // Only wrap with PullToRefresh in list mode
  if (viewMode === "list") {
    return <PullToRefresh onRefresh={handleRefresh}>{pageContent}</PullToRefresh>;
  }

  return pageContent;
};

export default Restaurants;